@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

.swiper {
  width: 1200px;
  height: 600px;
  margin-top: 60px;
  color: black;
}

.staff-span{
  font-weight: bolder;
  font-size: 20px;
}

.main--staff--div{
  width: 900px;
  display: flex;
  margin: auto;
  align-items: center;
  h2{
    // padding: 5px;
    margin: 0;
  }
  p{
    font-size: 15px;
    font-family: 'Poppins', sans-serif;
  }
  h5{
    padding: 5px;
  }
  .main--staff--div--sub--title{
    font-family: 'Poppins', sans-serif;
    margin: 0;
  }
  &--img{
    object-fit: cover;
    width: 350px;
    height: 400px;
    border-radius: 20px;
    margin-right: 30px;
  }
}
