.work--section{
    text-align: right;
    width: 550px;
    padding-top: 50px;
    padding-bottom: 50px;

    @media only screen and (max-width:1290px) {
        padding: 0;
    }

    &--content{
        padding-left: 15px;
        &--title{
            display: flex;
            justify-content: space-between;
            border: 1px solid #d0d0d0;
            border-radius: 4px;
            background: #fcfcfc;
            padding: 15px;
            border-radius: 20px;

            img{
                margin: 4px 24px 7px 0;
            }
            h3{
                font-size: 22px;
                line-height: 27px;
                margin: 0 0 7px;
            }
            p{
                font-size: 15px;
                color: #777;
            }
            @media only screen and (max-width: 1290px) {
                display: none;
            }
        }
    }

