.portfolio--section{
    background-color: #171717;
    border-top: 5px solid #384045;
    border-bottom: 5px solid #ccc;
    text-align: left;
    padding: 8px 0 6px;
    &--content{
        a{
            color: #28343c;
        }
        h5{
            font-size: 0.9rem;
            color: #777;
        }
    }
    &--title{
        // margin-bottom: -11px;
        // padding-bottom: 12px;
        h1{
            color: #ffff;
            margin: 0;
            font-size: 2.2em;
            font-weight: 200;
            z-index: 2;
            padding: 0 0 2px;
            line-height: 46px;
        }
    }
}